@import "../../../styles/vars";
@import "../../../styles/mixins";


.blogList {
  display: block;

  &__wrapper {
    display: grid;
    grid-template-columns: repeat(3,1fr);
    column-gap: 20px;
    row-gap: 40px;

    @include mdScreenMedia {
      grid-template-columns: repeat(2,1fr);
    }

    @include smScreenMedia {
      grid-template-columns: 1fr;
    }
  }

  &__btn {
    display: block;
    margin: 40px auto 0;
  }

  &__loader {
    width: 100%;
    height: 200px;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;

    svg {
      height: 400px!important;
    }
  }
}
